var onBridgeData = {};
function onBridgeReady(){
var timestamp=new Date().getTime();
WeixinJSBridge.invoke(
'getBrandWCPayRequest', {
"appId":onBridgeData.appid,     //公众号名称，由商户传入
"timeStamp":timestamp,         //时间戳，自1970年以来的秒数
"nonceStr":onBridgeData.nonce_str, //随机串
"package":"prepay_id="+onBridgeData.prepay_id,      //订单详情扩展字符串
"signType":"MD5",         //微信签名方式：
"paySign":onBridgeData.sign //微信签名
},
function(res){
console.log("onBridgeReady")
console.log(res)
if(res.err_msg == "get_brand_wcpay_request:ok" ){



}
});
}

